var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',{staticClass:"vision section"},[_c('div',{staticClass:"container"},[_c('div',{staticClass:"row text-center mb-5",attrs:{"data-aos":"fade-up","data-aos-delay":"100"}},[_c('div',{staticClass:"col-12"},[_c('img',{staticClass:"img-fluid",attrs:{"src":_vm.getImgUrl('ai_lifeboat2.jpg'),"alt":"AI: A Lifeboat on the Media Ocean"}}),_c('br'),_c('br'),_c('h1',[_vm._v("AI: A Lifeboat to navigate the Media Flood")]),_vm._m(0)])]),_c('div',{staticClass:"row text-center my-4 mt-5",attrs:{"data-aos":"fade-up","data-aos-delay":"200"}},[_c('div',{staticClass:"col-12"},[_c('h2',[_vm._v("Introduction")]),_vm._m(1),_c('p',[_vm._v(" Learning, creating, and interacting are all essential aspects of our lives as humans, but they are becoming increasingly harder to perform as the world becomes saturated with content and opportunities. Faced with all the knowledge that I can absorb online, with all the sounds that I can produce through libraries, and with all the threads that I can participate in, I find myself submerged by possibilities, which pushes me back into my comfort zone. How can we help humans thrive by offering new ways to interact with online content? Enabling these human activities in an easier way therefore becomes an essential challenge to correctly answer our human needs. Artificial Intelligence, by providing personalization, adaptability, and a better understanding of each individual, thus appears as a lifeboat, offering a way to safely and comfortably guide our journey through the unsettling flood of media content. By joining the Media Lab, I aim to build these novel AI integrations that can help make approaching "),_c('a',{staticClass:"vision-link",on:{"click":function($event){return _vm.scrollTo('theme1')}}},[_vm._v("Education")]),_vm._v(", "),_c('a',{staticClass:"vision-link",on:{"click":function($event){return _vm.scrollTo('theme2')}}},[_vm._v("Creativity")]),_vm._v(", and "),_c('a',{staticClass:"vision-link",on:{"click":function($event){return _vm.scrollTo('theme3')}}},[_vm._v("Social Interactions")]),_vm._v(" more comfortable. ")])])]),_c('div',{ref:"theme1",staticClass:"row text-left my-4 mt-5",attrs:{"data-aos":"fade-up","data-aos-delay":"300"}},[_c('div',{staticClass:"col-7"},[_c('h2',[_vm._v("Personalizing Education")]),_c('div',{staticClass:"content text-justify"},[_c('p',[_vm._v(" Today, the content of almost every class that has ever been taught is available with the push of a button. By browsing the internet, anyone can read Leonardo Da Vinci's biography on Wikipedia or learn about quantum physics by watching an MIT OpenCourseWare lecture on YouTube. However, the vast majority of this information comes in a standardized format, with similar assumptions of backgrounds, references, and cultures. In this situation, I believe that using AI can make the delivery of content more personalized to the unique needs of every student around the world, and allow them to browse content at their own pace, in their own style. For example, language models can learn which type of language to use, or which cultural references to employ, to better engage with students with different backgrounds and preferences. Generative AI can also allow us to create virtual avatars with which students can interact and ask questions, thus embodying the browsing of content into a human form, bringing it closer to personalized tutoring. ")]),_c('p',[_vm._v(" Together with MIT Media Lab Research Assistants Pat Pataranutaporn and Valdemar Danry, MIT Media Lab Professor Pattie Maes, MIT Media Lab Visiting Student Lavanay Thakral, and UCSB Assistant Professor Misha Sra, I worked on a paper called "),_c('span',{on:{"click":_vm.scrollAtTop}},[_c('router-link',{staticClass:"vision-link",attrs:{"to":"/projects/living-memories"}},[_vm._v(" 'Living Memories' ")])],1),_vm._v(", currently under review for IUI 2023. In this work, we designed an AI-powered system that allowed anyone to learn about the life of Leonardo Da Vinci in a personalized and compelling way, by generating a digital avatar of Da Vinci that people could question about anything they wanted to know. The AI was trained on Da Vinci's biography and was able to answer questions in an authentic and interesting way. The result of the user study we conducted showed that self-reported engagement increased significantly when people were offered the opportunity to talk to the Living Memory, displaying the benefits of embodied information. In the future, I would love to continue working on this technology by improving its authenticity, for instance by infusing knowledge into large language models with the help of symbolic models and knowledge graphs. ")]),_c('p',[_vm._v(" A requirement for these interactive AI interfaces to function appropriately is the existence of a common medium of communication, understood and used by both AI systems and human users. This common medium can be based on natural language, but also on cultural references or specialized theories. In the context of musical education, for example, I believe that AI systems should have an understanding of Music Theory. In "),_c('span',{on:{"click":_vm.scrollAtTop}},[_c('router-link',{staticClass:"vision-link",attrs:{"to":"/projects/neurosymbolic-music-classification"}},[_vm._v(" the application of my Master Thesis Project ")])],1),_vm._v(", I encoded Music Theory within a Neurosymbolic AI system and let it perform explainable music genre classification. The result was an interactive intelligence that was able to offer insights into the various theoretical aspects behind music genres, and help with the generation of new ideas. ")]),_c('p',[_vm._v(" In the future, I envision the current model of classrooms filled with tens of students becoming archaic, as the world turns increasingly towards personalized learning experiences that can focus on the needs, backgrounds, and preferences of every student. I believe that future teachers and educators will be able to manipulate AI technologies to create content that will be communicated to students in an adaptable way, by learning the strengths and weaknesses of every individual and modulating the delivery of the content accordingly. In general, I would like to work on augmenting learning experiences by helping AI systems gain complete knowledge of the internet through the use of knowledge graphs, and by constructing more personalized interfaces that can facilitate learning, such as digital avatars or immersive VR environments. ")])]),_vm._m(2),_c('isotope',{directives:[{name:"images-loaded",rawName:"v-images-loaded:on.progress",value:(_vm.layout),expression:"layout",arg:"on",modifiers:{"progress":true}}],ref:"portfolioTheme1",staticClass:"row no-gutter",attrs:{"options":_vm.options,"list":_vm.listTheme1}},_vm._l((_vm.listTheme1),function(project,index){return _c('div',{key:index,staticClass:"col-sm-6 col-md-4 col-lg-4 mb-4",on:{"click":_vm.scrollAtTop}},[_c('router-link',{staticClass:"item-wrap fancybox",attrs:{"to":("/projects/" + (project.id))}},[_c('div',{staticClass:"work-info px-1"},[_c('h2',[_vm._v(_vm._s(project.name))]),_c('span',[_vm._v(_vm._s(project.types.join(' | ')))]),_c('br'),_c('small',[_c('em',[_vm._v(_vm._s(project.date))])])]),_c('img',{staticClass:"img-fluid",attrs:{"src":_vm.getImgUrl(project.imgUrl)}})])],1)}),0)],1),_c('div',{staticClass:"col-5"})]),_c('div',{ref:"theme2",staticClass:"row text-left my-4 mt-5",attrs:{"data-aos":"fade-up","data-aos-delay":"300"}},[_c('div',{staticClass:"col-5"}),_c('div',{staticClass:"col-7"},[_c('h2',[_vm._v("Spreading access to Creativity")]),_c('div',{staticClass:"content text-justify"},[_c('p',[_vm._v(" As existing tools to create art have spread very fast, it is now possible for everyone to use the most high-end and professional software available on the market. However, this does not necessarily translate into creation being more accessible, as these tools have steep learning curves and were made with specific user flows in mind, which might not be adapted to everyone. I believe in creation being necessary for humans to express their thoughts and emotions, and, as such, I believe that everyone should have the ability to create art that is relatable, comfortable, and true to them. In particular, I am interested in developing tools that can help both amateurs and leading experts rediscover their musical creativity and express their musical ideas more easily. ")]),_c('p',[_vm._v(" Through my 2020 startup "),_c('span',{on:{"click":_vm.scrollAtTop}},[_c('router-link',{staticClass:"vision-link",attrs:{"to":"/projects/djstreamr"}},[_vm._v(" DJStreamr ")])],1),_vm._v(", I worked to enhance the art of DJing and improve its accessibility by developing a novel kind of creative connection for DJ performers online. By synchronizing multiple instances of our software, we allowed DJs to collaborate remotely and create live-streamed performances that could be listened to around the world. As part of this tool, we introduced novel technologies into the world of DJing, by implementing AI suggestions for tracks, effects, and AI-based sound processing. Performers were for example able to mash up vocals and instrumentals in creative ways, and navigate their DJ set depending on the desired emotional response of the crowd, and by following AI suggestions. This project therefore infused new creativity tools into an existing craft, and augmented its accessibility through the use of creative AI systems. ")]),_c('p',[_vm._v(" My ongoing project "),_c('span',{on:{"click":_vm.scrollAtTop}},[_c('router-link',{staticClass:"vision-link",attrs:{"to":"/projects/human-music"}},[_vm._v(" 'Human Music' ")])],1),_vm._v("seeks to make music creation possible through the interface of facial expressions and body language. The developed model uses Contrastive Learning and processes the user's facial expressions, body language, and words to generate music that matches corresponding detected emotions. By using the universal medium of the body and the face, I aim to make music creation as accessible as possible, and allow anyone to create music that can capture any emotion and moment that they might go through, and want to remember in another format in the future. Through this tool, I hope to enhance both the creativity of amateurs and professional musicians, by offering an inexhaustible source of inspiration in the studio. ")]),_c('p',[_vm._v(" Music comes from a deep emotional place and resonates differently with everyone. In the future, I envision music creation and consumption to be a fully personal and exclusive experience, expanding creative abilities to a much wider audience. Everyone, regardless of their level of expertise, will be able to create music that is true to them, that encapsulates a specific emotion or moment, and they will then be able to revisit it at any time. For example, digital photography could be augmented by helping users generate their own soundtrack for each picture, creating an audiovisual museum of memories that can be used for personal or therapeutic reasons. I would love to work more in depth with generative AI models and make them more personalizable and more knowledgeable about the style and preferences of every user. Currently, obtaining good results with generative AI requires good reverse-engineering skills to understand the intricacies of the model. By leveraging the user-adaptability of Neurosymbolic AI, I aim to make the process of communicating with models more interactive, and more adapted to the needs of every user. ")])]),_vm._m(3),_c('isotope',{directives:[{name:"images-loaded",rawName:"v-images-loaded:on.progress",value:(_vm.layout),expression:"layout",arg:"on",modifiers:{"progress":true}}],ref:"portfolioTheme2",staticClass:"row no-gutter",attrs:{"options":_vm.options,"list":_vm.listTheme2}},_vm._l((_vm.listTheme2),function(project,index){return _c('div',{key:index,staticClass:"col-