 ")])],1),_vm._v(" , together with Valdemar Danry from the MIT Media Lab. This project uses a Neurosymbolic AI architecture to understand human dialogue and model human reasoning, and aims at facilitating online conversations by keeping track of the flow of arguments and by helping people reason. By visualizing every argument on a visual graph, the system offers a novel way to navigate discussions, making it possible for users to detect logical contradictions, better structure their arguments, as well as understand their counterparts and build a more structured debate. By keeping track of the arguments, it can also help prevent people from repeating themselves, and help them better understand the other side's point of view. The system also offers a way to reason about the constructed graph, by highlighting certain aspects of the conversation, such as contradicting facts or logical inconsistencies. Another aim of the project is to augment conversations by providing automated insight into the implicit knowledge or references that might be necessary for understanding a certain conversation, therefore allowing anyone to join any conversation and any social interaction, regardless of their background or domain of expertise. ")]),_c('p',[_vm._v(" In the future, I envision thread-based, two-dimensional, social media quickly becoming obsolete. By using AI-assisted communication technologies, people will be able to virtually exchange ideas and information that will get automatically represented in visual ways, and with which other users will be able to interact. Generative AI could create 3D narrative virtual stories to help users understand other people's unique situations and points of view, or simulate historic events to help them catch up with necessary background knowledge. I envision each piece of information becoming part of 3D explorable graphs. Through these visual representations, anyone will be able to join any conversation, quickly catching up with the necessary background knowledge, and add their own opinion. I aim to work more with Neurosymbolic AI to understand conversations, to embed better common sense knowledge, and to use large language models to generate personalized summaries of discussions. ")])]),_vm._m(4),_c('isotope',{directives:[{name:"images-loaded",rawName:"v-images-loaded:on.progress",value:(_vm.layout),expression:"layout",arg:"on",modifiers:{"progress":true}}],ref:"portfolioTheme3",staticClass:"row no-gutter",attrs:{"options":_vm.options,"list":_vm.listTheme3}},_vm._l((_vm.listTheme3),function(project,index){return _c('div',{key:index,staticClass:"col-sm-6 col-md-4 col-lg-4 mb-4",on:{"click":_vm.scrollAtTop}},[_c('router-link',{staticClass:"item-wrap fancybox",attrs:{"to":("/projects/" + (project.id))}},[_c('div',{staticClass:"work-info px-1"},[_c('h2',[_vm._v(_vm._s(project.name))]),_c('span',[_vm._v(_vm._s(project.types.join(' | ')))]),_c('br'),_c('small',[_c('em',[_vm._v(_vm._s(project.date))])])]),_c('img',{staticClass:"img-fluid",attrs:{"src":_vm.getImgUrl(project.imgUrl)}})])],1)}),0)],1),_c('div',{staticClass:"col-5"})]),_vm._m(5)])])}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',[_c('i',[_vm._v("Building Intelligent Systems to allow comfortable access to media content")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',[_c('b',[_vm._v("We are being flooded by media content.")]),_vm._v(" As our technologies expand and get applied to an increasing number of activities, we are faced with more knowledge, creative opportunities, and social interactions than ever before. At the age of ten, I was able to learn how to code by myself using online tutorials, create my music and publish it online using Garage Band and Bandcamp, and connect with my friends using Facebook. However, technologies to navigate this content have not evolved nearly as fast. Watching tutorials on the internet remains one of the only ways to learn new skills, the software I used to create music has only barely evolved, and social media have steadily survived in their existing format. As the amount of available media content grows even bigger, diving into this flood of content requires more courage than ever. ")])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',[_c('span',{staticClass:"font-weight-bold"},[_vm._v("Related Projects:")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',[_c('span',{staticClass:"font-weight-bold"},[_vm._v("Related Projects:")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('p',[_c('span',{staticClass:"font-weight-bold"},[_vm._v("Related Projects:")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row text-center mb-5",attrs:{"data-aos":"fade-up","data-aos-delay":"100"}},[_c('div',{staticClass:"col-12"},[_c('br'),_c('br'),_c('h2',[_vm._v("Conclusion")]),_c('p',[_vm._v(" Throughout this statement of objectives, I drafted out the plans of the AI Lifeboat, an intelligent vessel that can help us navigate the Media Flood in a more reassuring and comfortable way. I strongly believe that AI can help us make Education more personalized, Creativity more accessible, and Social Interactions more democratized. By better integrating AI systems that can understand our needs and take into account our backgrounds, experiences, and preferences, we can build a more inclusive and safe way of venturing into the immense world of media. As an AI researcher, engineer, and artist myself, I aim to create innovative systems that can help everyone explore, discover, and create in comfort. ")])])])}]

